<template>
  <div id="app">
    <h1>My To-Do List</h1>
    <to-do-form @todo-added="addToDo">
      <el-button type="primary" @click="addToDo">Add</el-button>
    </to-do-form>
    <h2 id="list-summary" ref="listSummary" tabindex="-1">{{listSummary}}</h2>
    <ul aria-labelledby="list-summary" class="stack-large">
      <li v-for="item in ToDoItems" :key="item.id">
  <div v-if="!item.isEditing">
    <to-do-item
      :label="item.label"
      :done="item.done"
      :id="item.id"
      @checkbox-changed="updateDoneStatus(item.id)"
      @item-deleted="deleteToDo(item.id)"
      @item-edited="editToDo(item.id, $event)"
    ></to-do-item>
  </div>
  <div v-else>
    <to-do-item-edit-form
    :id = "item.id"
    :label="item.label"
    @item-edited="editToDo(item.id, $event)"
    @edit-cancelled="cancelEdit(item.id)"
    ></to-do-item-edit-form>
  </div>
</li>
    </ul>
    <div id="chart"></div>
  </div>
</template>

<script>
import ToDoItem from "./components/ToDoItem.vue";
import ToDoForm from "./components/ToDoForm.vue";
import { ElButton } from 'element-plus';
import * as echarts from 'echarts';
import { nanoid } from "nanoid";

export default {
  name: "app",
  components: {
    ToDoItem,
    ElButton,
    ToDoForm,
  },
  data() {
    return {
      ToDoItems: [
        { id: "todo-" + nanoid(), label: "Learn Vue", done: false },
        {
          id: "todo-" + nanoid(),
          label: "Create a Vue project with the CLI",
          done: true,
        },
        { id: "todo-" + nanoid(), label: "Have fun", done: true },
        { id: "todo-" + nanoid(), label: "Create a to-do list", done: false },
      ],
    };
  },

  methods: {
    addToDo(toDoLabel) {
      this.ToDoItems.push({
        id: "todo-" + nanoid(),
        label: toDoLabel,
        done: false,
      });
    },
    updateDoneStatus(toDoId) {
      const toDoToUpdate = this.ToDoItems.find((item) => item.id === toDoId);
      toDoToUpdate.done =!toDoToUpdate.done;
    },
    deleteToDo(toDoId) {
      const itemIndex = this.ToDoItems.findIndex((item) => item.id === toDoId);
      this.ToDoItems.splice(itemIndex, 1);
      this.$refs.listSummary.focus();
    },
    editToDo(toDoId, newLabel) {
      const toDoToEdit = this.ToDoItems.find((item) => item.id === toDoId);
      toDoToEdit.label = newLabel;
    },
     cancelEdit(toDoId) {
     const toDoToCancelEdit = this.ToDoItems.find((item) => item.id === toDoId);
     toDoToCancelEdit.isEditing = false;
    },
    initChart() {
    const chartDom = document.getElementById('chart');
    const myChart = echarts.init(chartDom);
    const completedCount = this.ToDoItems.filter(item => item.done).length;
    const totalCount = this.ToDoItems.length;
    const option = {
        title: {
            text: '待办事项完成情况'
        },
        series: [
            {
                name: '完成情况',
                type: 'pie',
                data: [
                    { value: completedCount, name: '已完成' },
                    { value: totalCount - completedCount, name: '未完成' }
                ]
            }
        ],
        dataZoom: [
            {
                type: 'inside',
                start: 0,
                end: 100
            }
        ]
    };
    myChart.setOption(option);
},},
  computed: {
    listSummary() {
      const numberFinishedItems = this.ToDoItems.filter((item) => item.done).length;
      return `${numberFinishedItems} out of ${this.ToDoItems.length} items completed`;
    }
  },
  mounted() {
    this.initChart();
    // 添加监听器，监听 ToDoItems 数据变化，当变化时重新调用 initChart 方法更新图表
    this.$watch('ToDoItems', () => {
      this.initChart();
    }, { deep: true });
  }
};
</script>


